:root {
  --radius: 3px;
  --blue: #5a6b88;
  --hover-color: #f19c44;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  color: #555;
}

h1, h2, h3, h4, h5, h6 {
  color: #777;
}

.container {
  max-width: 900px;
  margin: 0 auto;
}

.App {
  max-width: 900px;
  min-height: 100vh;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.post-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 10px;
  background-color: #5a6b88;
  background-color: var(--blue);
  border-radius: 3px;
  border-radius: var(--radius);
}
.post-header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-item {
  width: 33.33%;
}
.nav-item button {
  border: 1px solid #fff;
  color: #fff;
}
.nav-item button:hover {
  background-color: #f19c44;
  background-color: var(--hover-color);
  border-color: #f19c44;
  border-color: var(--hover-color);
}
.post-header__title {
  color: #fff;
  text-align: center;
}

.add-btn {
  margin: 10px 0;
}

.post-form {
  padding: 10px 0;
}
.post-form__title {
  padding-bottom: 20px;
  text-align: center;
}



.post-list {
  flex-grow: 1;
  padding: 60px 0 10px;
}

.post-list__loader {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.post-list__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.post-list__title {
  padding: 10px 0;
  text-align: center;
}

.post {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding: 15px;
  /*border: 1px solid var(--blue);*/
  border-radius: 3px;
  border-radius: var(--radius);
  box-shadow: 0 0 3px #5a6b88;
  box-shadow: 0 0 3px var(--blue);
}

.post-enter {
  transform: translateX(-300px);
}
.post-enter-active {
  transform: translateX(0);
  transition: all 300ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-in;
}

.post__btns {
  flex-shrink: 0;
}


/* ------------------------ Footer ---------------------- */
.post-footer {
  background-color: #5a6b88;
  background-color: var(--blue);
}
.post-footer p {
  text-align: center;
  color: #bbb;
  font-size: 14px;
  padding: 10px;
}
.post-footer p a {
  color: #45b29a;
  transition: 0.5s;
  text-decoration: underline;
}
.post-footer p a:hover {
  color: #f19c44;
  color: var(--hover-color);
  text-shadow: 0 0 2px #f19c44, 0 0 10px #f19c44;
  text-shadow: 0 0 2px var(--hover-color), 0 0 10px var(--hover-color);
}


/* ------------- Responsive -------------- */
@media (max-width: 900px) {
  .post-list {
    padding: 60px 10px 10px;
  }
}

@media (max-width: 720px) {
  .post {
    flex-direction: column;
    align-items: flex-start;
  }

  .post button {
    margin-top: 10px;
  }
}

